<template>
  <div class="cv">
    <Row style="height:200px;background-color:#202634;color:white;">
        <Col span="8" style="padding-left:80px;">
            <div class="logo">
                <img src="../assets/wavin.png"  class="wavin-logo"/>
            </div>
        </Col>
        <Col span="16" style="text-align:left;">
            <div class="basic-info">
                <h1 style="color:#FFDFA1;">{{basicInfo.name}}</h1>
                <div class="info">
                    <ul>
                        <li>{{basicInfo.gender}} | Birthday: {{basicInfo.birthday}} | {{basicInfo.location}}</li>
                        <li><span><i class="iconfont icon-dianhua"></i> {{basicInfo.phone}}</span> <span><i class="iconfont icon-youxiang1"></i> {{basicInfo.email}}</span></li>
                        <li>On the job(within one month) | {{basicInfo.experience}} years' experiences | {{basicInfo.job}} | Excepted Salary: {{basicInfo.salary}}</li>
                    </ul>
                </div>
            </div>
        </Col>
    </Row>
    <Row style="margin-top:25px;">
        <Col span="8" style="text-align:left;padding-left:20px;">
            <i class="splitor"></i>
            <i class="iconfont icon-zhuanyejineng"></i> 
            <span class="wavin-label">Professional Abilities</span>
        </Col>
        <Col span="16" style="text-align:left;">
            <div class="wavin-ordered-list">
                <ul>
                    <li v-for="i in skills">{{i}}</li>
                </ul>
            </div>
        </Col>
    </Row>

    <Row style="margin-top:25px;">
        <Col span="8" style="text-align:left;padding-left:20px;">
            <i class="splitor"></i>
            <i class="iconfont icon-suyuanmaguanli"></i> 
            <span class="wavin-label">Sources Reading</span>
        </Col>
        <Col span="16" style="text-align:left;">
            <div class="wavin-ordered-list">
                <ul>
                    <li v-for="i in sourcecodes">{{i}}</li>
                </ul>
            </div>
        </Col>
    </Row>

    <Row style="margin-top:25px;">
        <Col span="8" style="text-align:left;padding-left:20px;">
            <i class="splitor"></i>
            <i class="iconfont icon-13"></i> 
            <span class="wavin-label">Social Homepage</span>
        </Col>
        <Col span="16" style="text-align:left;">
            <div class="wavin-ordered-list">
                <ul>
                    <li v-for="i in socialpages">{{i}}</li>
                </ul>
            </div>
        </Col>
    </Row>

    <Row style="margin-top:25px;">
        <Col span="8" style="text-align:left;padding-left:20px;">
            <i class="splitor"></i>
            <i class="iconfont icon-pingjia"></i> 
            <span class="wavin-label">Self Evaluation</span>
        </Col>
        <Col span="16" style="text-align:left;">
            <div style="color:black;">
                {{selfeval}}
            </div>
        </Col>
    </Row>

    <Row style="margin-top:25px;">
        <Col span="8" style="text-align:left;padding-left:20px;">
            <i class="splitor"></i>
            <i class="iconfont icon-gongzuojingli"></i> 
            <span class="wavin-label">Work Experiences</span>
        </Col>
        <Col span="16" style="text-align:left;">
            <div class="wavin-disorderly-list">
                <ul>
                    <li v-for="(experience,i) in experiences" :style=" i == 0 ? '':'margin-top:30px;'">
                        <div class="experience">
                            <span class="companyName">{{experience.companyName}}</span>
                            <span>{{experience.post}}</span>
                            <span>{{experience.start}} - {{experience.end}}</span>
                        </div>
                        <div style="padding-top:10px;">
                            <span class="right-title" v-if="experience.tech">Using Technologies</span>
                            <span>{{experience.tech}}</span>
                        </div>
                        <div>
                            <span class="right-title">Job Content</span>
                            <ul class="work">
                                <li v-for="content in experience.contents">{{content}}</li>
                            </ul>
                        </div>
                        <div style="margin-top:20px;">
                            <span class="right-title">Contributions</span>
                            <ul class="tacklings">
                                <li v-for="tackling in experience.tacklings">{{tackling}}</li>
                            </ul>
                        </div>
                    </li>
                </ul>
            </div>
        </Col>
    </Row>

    <Row style="margin-top:25px;">
        <Col span="8" style="text-align:left;padding-left:20px;">
            <i class="splitor"></i>
            <i class="iconfont icon-logo_school"></i> 
            <span class="wavin-label">Graduated From</span>
        </Col>
        <Col span="16" style="text-align:left;">
            <div class="wavin-nostyle-list">
                <ul>
                   <li>
                       <span class="university">{{studyInfo.university}}</span>
                       <span class="university-normal">{{studyInfo.universityAttr}}</span>
                       <span class="university-normal">{{studyInfo.major}}</span>
                       <span class="university-normal">{{studyInfo.majorStart}} - {{studyInfo.majorEnd}}</span>
                    </li>
                   <li v-for="item in studyInfo.projects">{{item}}</li>
                </ul>
            </div>
        </Col>
    </Row>

  </div>
</template>

<script>
export default {
  name: 'JavaCV_en',
  data () {
    return {
        basicInfo:{
            name: "Wavin Don",
            gender: "Male",
            birthday: "1996/10",
            location: "Shanghai",
            phone: "17603070784",
            email: "de1653267893@foxmail.com",
            experience: "3",
            job: "Java",
            salary: "20-24k",
            
        },
        studyInfo:{
            university: "HuaiHua University",
            universityAttr: "Undergraduate Course",
            major: "Software Engineering",
            majorStart: "2014",
            majorEnd: "2018",
            projects:[
                "1. Join the creative base, and had developed an collector APP for Android.",
                "2. Participate the open source projects with teacher."
            ]
        },
        selfeval: "Strong learning ability, be able to independently complete small projects or a module of large project, hign work efficiency.",
        skills: [
            "Be good at Linux, and write shell scripts.",
            "Using Java skillfully,  including but not limited to Collections, IO, Thread Pool, JUC, and Other basic principles, etc.",
            "Have experiences for using Java frameworks like SpringBoot, mybatis. and have developed a springboot starter.",
            "Know abount how to use netty, and customize protocols using netty.",
            "Know such middleware like redis, rabbitmq.",
            "Skilled for SQL script sytax, know about structure for MySQL.",
            "Know a bit of job schedulers.",
            "Be proficient in protocols like RPC, HTTP.",
            "Understand spring cloud alibaba and it's components like Dubbo/Nacos.",
            "Be able to develop html/css, vue and jquery,etc."
        ],
        sourcecodes:[
            "Have learned source code of Linux",
            "Have researched pbx source code in more depth and develop based it.",
            "Have learned the jdk source code."
        ],
        socialpages:[
            "CSDN Blog: http://csdn.wdcc.top/",
            "GitHub: http://github.wdcc.top/",
            "Gitee: http://gitee.wdcc.top/"
        ],
        experiences:[
            {
                companyName: "Gientech",
                post: "Java",
                start: "2019/11",
                end: "Now",
                contents: [
                    "Providing implementing and deploying solutions for platform modules",
                    "Providing high-availability, hot standby and load balance solutions for the CC voice platform",
                    "Developing platform SDKs",
                    "Packaging and secondary development based on platform."
                ],
                tacklings:[
                    "Developed core module named CC API",
                    "Bootstraping a conference system based on WebRTC",
                    "Fixed the problem that platform connect to other modules repeatedly",
                    "Wrote API document, and software design document.",
                    "Developed and refactor Log subsystem",
                    "Wrote shell scripts for one key manage",
                    "Resolved the problem which database has residue data in table",
                    "Resolved dead lock problem due to wrong using Promise",
                    "Developed a customize sip phone using NodeJS and C++"
                ],
                tech: "Java、 SpringBoot、 MyBatis Plus、 Redis、 RabbitMQ、 MySQL、 ESL、netty"
            },
            {
                companyName: "Shanghai GongRui Information and Technology",
                post: "Java",
                start: "2018/09",
                end: "2019/10",
                contents: [
                    "Deploying a frontend of trade system to production and make it online",
                    "Connecting to core of bank, Enterprise Service Bus and other systems",
                    "Using open source component to transfer oneside database to otherside",
                    "Developing plugins for platform like encrypt plugin",
                ],
                tacklings:[
                    "Made first system online",
                    "Resolved TCP half-packages/sticky-packages problem for netty",
                    "Resolved netty discard the packages",
                    "Resolved program crash due to not enough file handle for server",
                ],
                tech: "Java、 SpringBoot、 MyBatis Plus、 MySQL、netty、 XXL Job"
            },

            {
                companyName: "Changsha ShangQuan Information and Technology (实习)",
                post: "Engineer",
                start: "2018/03",
                end: "2018/09",
                contents: [
                    "Installing and maintaining Tele-selling system for customers",
                    "Resolving problems for customers",
                    "Optimizing cloud platforms"
                ],
                tacklings:[
                    "Help for cloud data transfer",
                    "Install and test software and hardware for thousands of customers' agent",
                    "Monitor legality for customers' operations",
                    "Help customer to building their own platform account",
                ]
            },
            {
                companyName: "ShenZhen Costar Software (见习)",
                post: "C master Programer",
                start: "2017/7",
                end: "2017/11",
                contents: [
                    "Developing and maintaining for mail subsystem",
                    "Developing new game copies",
                    "Fixing Bugs for client and server"
                ],
                tacklings:[
                    "Completed a game copy named Richer Man",
                    "Implemented client tool bar flashing",
                    "Used lua script to create new window for new copy",
                    "Used lua script to make mail system show correct data"
                ]
            }
        ],

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cv{
    width: 800px;
    height: 3600px;
    background-color: white;
    display: inline-block;
    padding-top: 50px;
}
.logo {
    text-align: center;
    margin-top: 25px;
    width: 154px;
    height:154px;
    padding:1px;
    background-color: white;
}
.wavin-logo{
    width: 150px;
    height:150px;
}
.basic-info {
    padding-top: 30px;
}
.info{
    padding-top: 10px;
}
.info>ul, .info>ul>li{
    list-style: none;
}
.info>ul>li{
    padding: 5px;
}

.info>ul>li>span{
    padding-right: 20px;
}
.wavin-label{
    text-align: left;
    padding-left: 30px;
    font-size: 18px;
    font-weight: bold;
    color: black;
}
.wavin-ordered-list>ul{
    list-style: decimal;
}

.wavin-disorderly-list>ul{
    list-style: square;
}

.splitor{
    width: 1px;
    height: 50px;
    background-color: #202634;
}
.experience>.companyName{
    font-size: 16px;
    font-weight: bold;
    color: black;
}
.experience>span{
    padding-right: 20px;
}
.work{
    margin-top: 20px;
    list-style: decimal;
}

.right-title{
    position: absolute;
    left: -130px;
}

.wavin-nostyle-list, .wavin-nostyle-list>ul, .wavin-nostyle-list>ul>li{
    list-style: none;
}

.university{
    font-size: 18px;
    font-weight: bold;
    color: black;
}
.university-normal {
    padding-left: 40px;
    color: black;
}
</style>



