import Vue from 'vue'
import Router from 'vue-router'
import JavaCV_zh from '@/components/JavaCV_zh'
import JavaCV_en from '@/components/JavaCV_en'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'JavaCV_zh',
            component: JavaCV_zh
        },
        {
            path: '/zh_cn',
            name: 'JavaCV_zh',
            component: JavaCV_zh
        },
        {
            path: '/en_us',
            name: 'JavaCV_en',
            component: JavaCV_en
        }
    ]
})


